@import "var";
@import "function";
@import "mixin";
@import "reset";

@font-face {
    font-family: 'SIMYOU';
    src: url('../font/SIMYOU.eot'); /* IE9 Compat Modes */
    src: url('../font/SIMYOU.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('../font/SIMYOU.woff') format('woff'), /* Modern Browsers */
         url('../font/SIMYOU.ttf')  format('truetype'), /* Safari, Android, iOS */
         url('../font/SIMYOU.svg#SIMYOU') format('svg'); /* Legacy iOS */
    src: '../font/SIMYOU.ttf';
  }

.rDialog {
   width: 100%;
   position: fixed;
   bottom:px2rem(80);
}

.rDialog-wrap {
  position: relative;
  background: #000;
  background-clip: padding-box;
  border-radius: px2rem(10);
  box-shadow: px2rem(1) px2rem(1) px2rem(1) #000;
  padding:px2rem(30) px2rem(15);
  margin: 0 px2rem(15);
}
.rDialog-header-load {
  text-align: center;
  background: url(../img/alert-loading.gif);
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: 100%;
  width: px2rem(50);
  height: px2rem(53);
  margin: auto;
}
.rDialog-header-alert,.rDialog-header-ok {
  text-align: center;
  background: url(../img/alert-icons.png);
  background-repeat: no-repeat;
  background-size: 100%;
  width: px2rem(50);
  height: px2rem(53);
  margin: auto;
}
.rDialog-header-alert{
  background-position: 0 px2rem(-53);
}
.rDialog-header-ok {
  background-position: 0 0;
}
.rDialog-content {
    padding: px2rem(10) px2rem(15) px2rem(10) px2rem(0);
    font-size: font-dpr(0.8);
    color: #FFF;
    overflow: hidden;
    text-align: center;
}
.rDialog-footer {
    overflow: hidden;
}
.rDialog-footer a {
    text-align: center;
    width:px2rem(326);
    height: px2rem(90);
    line-height: px2rem(90);
    font-size: font-dpr(30);
    border-radius: px2rem(3);
    background-image: none;
    color: #000;


}


.rDialog-ok {
    float:right;
    background: #fff;
}
.rDialog-cancel {
    float: left;
    background: #ddd;
}
.rDialog-mask {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: #000;
    opacity: .4;
}


textarea.textInput{
  border:px2rem(6) solid #00A0E9;
  height:px2rem(80);
  font-size:px2rem(30);
  width:100%;
  display: inline;
}


